// --------------------------------------------------
//		Components: Page Header
// --------------------------------------------------

@use 'helper' as *;
@use 'tools' as *;

.singleHeader {
	display: flex;
	row-gap: 15px;
	flex-direction: column;

	@extend %module-top-medium;
	@extend %module-bottom-small;
	border-bottom: 1px solid color('gray', 'light');

	@include up(md) {
		flex-direction: row;
		align-items: center;
		column-gap: 30px;
	}

	img {
		object-fit: contain;
		height: 40px;
		align-self: flex-start;

		@include up(md) {
			height: 80px;
		}
	}

	h1 {
		font-weight: 500;
		line-height: 1em;
		text-transform: uppercase;
	}
}
