// --------------------------------------------------
//		Elements: Entities
// --------------------------------------------------

@use 'settings' as *;
@use 'tools' as *;

* {
	box-sizing: border-box;
}

// html
html {
	overflow-x: hidden !important;
}

// body
body {
	position: relative;
	overflow-x: hidden !important;
	-webkit-font-smoothing: antialiased;

	@extend %font-base;
	@extend %body-horizontal-medium;

	background-color: $body-bg;
	color: $body-color;
}

// paragrapgh
p,
li,
a,
small,
button {
	@extend %font-base;
	color: color('black');
	line-height: $line-height-base;
}

p,
li,
a,
figure {
	margin: 0;
	padding: 0;
}

// strong
strong {
	font-weight: 600;
}

// links
a {
	color: $link-color;
	text-decoration: $link-decoration;
	transition: all 0.3s;
	&:hover {
		color: $link-hover-color;
	}
}

// headings
h1,
h2,
h3,
h4,
h5,
h6 {
	padding: 0;
	margin: 0;
}

ul,
li {
	text-decoration: none;
	padding: 0;
	margin: 0;
	list-style: none;
}
